const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
const NEWS_URL = 'http://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'http://api.hnpwa.com/v0/item/@id.json'

// Ïó¨Îü¨ Ìï®ÏàòÏóê Í±∏Ï≥êÏÑú Ï†ëÍ∑ºÌïòÍ≤å ÎêòÎäî Ï†ïÎ≥¥Îäî Ìï®Ïàò Î∞îÍπ•Ï™ΩÏúºÎ°ú ÎπºÎÜìÎäî Í≤ÉÏù¥ ÌïÑÏöîÌï®
const store = {
    currentPage: 1,
    // ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏Î•º Ïù¥Ïö©Ìïú ÌéòÏù¥Ïßï Ï≤òÎ¶¨ Î∞©Î≤ï

    // ÌéòÏù¥Ïßï ÌååÎùºÎØ∏ÌÑ∞
    // appendEle : Element
    // totalCount : Îç∞Ïù¥ÌÑ∞ Ï¥ù Ïπ¥Ïö¥Ìä∏
    // recordsPerPage : ÌéòÏù¥ÏßÄ Îç∞Ïù¥ÌÑ∞ Î†àÏΩîÎìú Í∞úÏàò
    // navPage : ÌéòÏù¥ÏßÄ Í∞úÏàò
    // currentPage : ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
    // sellBoolean : Îß®Ïïû, Îß®Îí§ ÌëúÌòÑ Ïó¨Î∂Ä
}

function getData(url) {
    ajax.open('GET', url, false);
    ajax.send();

    return JSON.parse(ajax.response);
}

// Í∏Ä Î™©Î°ù
function newsFeed(){
    const newsFeed = getData(NEWS_URL);
    const newsList = [];
    newsList.push('<ul>');

    // currentPageÍ∞Ä 1Ïùº ÎïåÎäî iÍ∞íÏù¥ 0Ïù¥ÎØÄÎ°ú -1ÏùÑ Ìï¥Ï§ÄÎã§.
    // ÌïòÎÇòÏùò ÌéòÏù¥ÏßÄÏùò ÌÅ¨Í∏∞Í∞Ä Í≤∞Íµ≠ 10Í∞ú
    // 10Ïù¥ÎùºÎäî Í≤ÉÏùÄ Í≤∞Íµ≠ Ìïú ÌéòÏù¥ÏßÄÏùò Îã®ÏúÑÍ∞Ä Îê®
    // Í∑∏Îü¨ÎãàÍπå ÌéòÏù¥ÏßÄÍ∞Ä Î∞îÎÄêÎã§Îäî Í±¥ Ïù¥ ÌéòÏù¥ÏßÄ Îã®ÏúÑ Í∞úÏàòÎßåÌÅº ÎÑêÎõ∞Í∏∞Í∞Ä ÎêòÏñ¥Ïïº ÌïúÎã§Îäî Í≤É
    // for(let i=0; i<10; i++){
    for(let i=(store.currentPage - 1) * 10; i<store.currentPage * 10; i++){
        newsList.push(`
        <li>
            <a href="#/show/${newsFeed[i].id}">
            ${newsFeed[i].title} (${newsFeed[i].comments_count})
            </a>
        </li>
        `);
    }
    newsList.push('</ul>');
    newsList.push(`
        <div>
            <a href="#/page/${store.currentPage > 1 ? store.currentPage - 1 : 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
            <a href="#/page/${store.currentPage + 1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
        </div>
    `)
    // ÏΩîÎìúÍ∞Ä ÌïúÏ§ÑÏùºÎïê ifÎ¨∏ Ïì∞Í∏∞ Î∂ÄÎã¥Ïä§Îü¨Ïö∞Îãà ÏÇºÌï≠Ïó∞ÏÇ∞ÏûêÎ•º ÏÇ¨Ïö©ÌïúÎã§

    container.innerHTML = newsList.join('');
}

const ul = document.createElement('ul');

// Í∏Ä ÎÇ¥Ïö©
function newsDetail(){
    const id = location.hash.substr(7);
    const newsContent = getData(CONTENT_URL.replace('@id', id));
    const title = document.createElement('h1');

    container.innerHTML = `
        <h1>${newsContent.title}</h1>
        <div>
            <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
        </div>
    `;
}

// ÎùºÏö∞ÌÑ∞
function router(){
    const routePath = location.hash;

    if(routePath === ''){
        newsFeed();
    } else if (routePath.indexOf('#/page/') >= 0){  // 0Î≥¥Îã§ ÏûëÏùÄ Í∞íÏù¥Î©¥ ÏûÖÎ†•ÏúºÎ°ú Ï£ºÏñ¥ÏßÑ Î¨∏ÏûêÏó¥Ïù¥ ÏóÜÎã§
        // substrÏù¥ÎùºÍ≥† ÌïòÎäî Ìï®ÏàòÎ°ú Î∞òÌôòÎêòÎäî Í∞íÏùÄ Ïã§Ï†úÎ°ú Ïà´ÏûêÍ∞Ä ÏïÑÎãàÎùº Î¨∏ÏûêÏó¥Ïù¥Îã§
        // currentPageÎäî Ïà´ÏûêÎ°ú ÎêòÏñ¥ ÏûàÏñ¥Ïïº Ìï®
        // store.currentPage = 2;
        store.currentPage = Number(routePath.substr(7));
        newsFeed();
    } else {
        newsDetail();
    }
}

window.addEventListener('hashchange', router);

router();